.statusCodePage{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url(../img/failbg.png);
    background-size: 100% 100%;
    .errorPageMain{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        .defaultError{
            margin: 0 auto;
            width: 320px;
            height: 300px;
            >img{
                width: 100%;
                height: 100%;
                display: block;
            }
        }
        .error404{
            width: 320px;
            height: 300px;
        }
        .pageTips{
            margin: 32px 0 40px;
            font-size: 18px;
            line-height: 18px;
            color: #606266;
        }
        .errorBackBtn{
            font-size: 0;
            text-align: center;
            .btnEl{
                width: 90px;
                height: 34px;
                line-height: 34px;
                text-align: center;
                border: 1px solid #d9d9d9;
                border-radius: 3px;
                background: #fff;
                cursor: pointer;
                margin: 0 auto;
                &:not(:last-child){
                    margin-right: 35px;
                }
                .btnIcon{
                    width: 14px;
                    height: 14px;
                    margin-right: 5px;
                    background: url('../img/backIcon.png') no-repeat;
                    background-size: 100% 100%;
                }
                .btnName{
                    font-size: 14px;
                    color: #606266;
                }
                &.returnPreLevel{
                    background: #0097ff;
                    border: 1px solid #0097ff;
                    .btnIcon{
                        width: 9px;
                        background: url('../img/preLevel.png') no-repeat;
                        background-size: 100% 100%;
                    }
                    .btnName{
                        color: #fff;
                    }
                }
            }
        }
    }
}